import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from "@/components/HelloWorld.vue";
import RiskBlueprint from "@/components/RiskBlueprint.vue";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/riskblueprint',
      name: 'RiskBlueprint',
      component: RiskBlueprint
    }
  ]
})


